      // console.log(id);
      if (this.app === "Sql") {
        this.sqlConnectionId = id;
        this.$router.push({
          name: "sql-browse",
          query: { id },
        });
      } else if (this.app === "API") {
        this.apiConnectionId = id;
        this.$router.push({
          name: "api-browse",
          query: { id },
        });
      } else if (this.app === "slack") {
        this.slackConnectionId = id;
        this.$router.push({
          name: "slack-browse",
          query: { id },
        });
      } else if (this.app === "teams") {
